.movie {
    background-color: white;
    margin-bottom: 50px;
    font-weight: 400;
    padding: 15px;
    border-radius: 5px;
    color: black;
    display: grid;
    grid-template-columns: minmax(200px, 1fr) 3fr;
    grid-gap: 10px;
    text-decoration: none;
    color: inherit;
    background-color: lightgray;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
        0 8px 16px -8px rgba(0, 0, 0, 0.3),
        0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__img {
    position: relative;
    top: -30px;
    max-width: 300px;
    width: 90%;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
        0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie__title {
    margin: 0;
    font-size: 20px;
    font-weight: 700;
    text-decoration: none;
}

.movie__title a {
    text-decoration: none;
    color: black;
}

.movie__genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
}

.movie__genres li {
    font-weight: bold;
    color: gray;
}
